  methods: {
    onLoad() {
      let 
      areaParams={};


      let num=0;
      this.route.params.area.map( (item,index) => {
          if(index%2==1){
              num++;
              // let tempkey = ''+num
              // areaParams[num] = Number(this.route.params.area[index]['flag'])  + Number(this.route.params.area[index-1]['flag'])
              let cflag = Number(this.route.params.area[index-1]['flag']),
              tflag = Number(this.route.params.area[index]['flag']) ;
              // console.log(cflag, tflag )
              if( cflag && tflag  ){
                    areaParams[num] = 3
              }
              else if( cflag && !tflag  ){
                      areaParams[num] = 2
              }else if(! cflag && tflag   ){
                    areaParams[num] = 1
              }else if(! cflag && !tflag   ){
                    areaParams[num] = 0
              }
          }  
      });
      // console.log(areaParams);

      let formdata = new FormData();
      formdata.append('serial',this.route.params.serial)
      formdata.append('unit', JSON.stringify(areaParams) )

      let params={
        serial :this.route.params.serial,
         unit:areaParams
      }




      report(formdata).then((response) => {     
        //  console.log( response )
        this.res = response

      });
    },
  },
};
